<template>
  <div>
    <el-button type="primary" @click="add">添加</el-button>
    <!-- 添加子组件 -->
    <v-add :info="info" ref="add"></v-add>
    <!-- 添加列表组件 -->
    <v-list @detail="detail($event)"></v-list>
  </div>
</template>

<script>
import vAdd from "./add";
import vList from "./list"
export default {
  data() {
    return {
      info: {
        showAdd: false,
        title:"",
        //控制添加按钮和编辑按钮的显示和隐藏
        isAdd:true
      },
    };
  },
  methods:{
      add(){ 
          this.info.showAdd=true
          this.info.isAdd=true
          this.info.title="添加角色"
      },
      detail(id){
          this.$refs.add.roleDetail(id);
          this.info.showAdd=true
          this.info.isAdd=false
          this.info.title="编辑角色"
      },
  },
  components: {
    vAdd,
    vList
  },
};
</script>

<style scoped>
.el-button {
  margin: 20px 0;
}
</style>>



