<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.showAdd">
      <el-form :model="role">
        <el-form-item label="角色名称" :label-width="formLabelWidth">
          <el-input v-model="role.rolename"></el-input>
        </el-form-item>
        <el-form-item label="角色权限" :label-width="formLabelWidth">
          <el-tree
            :data="form"
            show-checkbox
            node-key="id"
            :default-checked-keys="defaultKey"
            :props="defaultProps"
            ref="tree"
          >
          </el-tree>
        </el-form-item>
        <el-form-item label="角色状态" :label-width="formLabelWidth">
          <el-switch
            v-model="role.status"
            active-color="#409eff"
            inactive-color="#eaecf0"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button @click="ok" type="primary" v-if="info.isAdd">添 加</el-button>
        <el-button @click="edit" type="primary"  v-else>编 辑</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../util/alert";
import { addRole,roleInfo,editRole,delRole} from "../../util/request";
export default {
  props: ["info"],
  data() {
    return {
      role: {
        rolename: "",
        menus: "",
        status: 1,
      },
      defaultKey: [],
      data: [],
      defaultProps: {
        // children: "children",
        label: "title",
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      form: "menu/getmenuList",
    }),
  },
  methods: {
    ...mapActions({
      "menuList": "menu/menuListActions",
      "roleList": "role/roleActions",
      roleDetail(a,id){
        roleInfo({id:id}).then(res=>{
          this.role=res.data.list
          this.defaultKey=this.$refs.tree.setCheckedKeys(JSON.parse(this.role.menus))
          this.role.id=id
        })
      },
      edit(){
        this.role.menus=JSON.stringify(this.$refs.tree.getCheckedKeys())
        editRole(this.role).then(res=>{
          if(res.data.code==200){
            successAlert(res.data.msg);
            this.cancel();
            this.roleList();
          }else{
            warningAlert(res.data.msg)
          }
        })
      },
      
    }),
    cancel() {
      this.info.showAdd = false;
      this.role = {
        rolename: "",
        menus: "",
        status: 1,
      };
       this.defaultKey=this.$refs.tree.setCheckedKeys([])
    },
    ok() {
      this.role.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
      addRole(this.role).then((res) => {
        if (res.data.code == 200) {
          this.roleList();
          this.cancel()
          successAlert(res.data.msg);
        }else{
          warningAlert(res.data.msg)
        }
      });
    },
  },
  mounted() {
    this.menuList();
    this.roleList();
  },
};
</script>

<style>
</style>


