<template>
  <div>
    <el-table
      :data="roleList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
    >
      <el-table-column prop="id" label="角色编号" width="120">
      </el-table-column>
      <el-table-column prop="rolename" label="角色名称"> </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1" @click="scope.row.status=2">启用</el-button>
          <el-button type="danger" v-else @click="scope.row.status=1">禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { delRole } from "../../util/request";
import { successAlert } from "../../util/alert";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      roleList: "role/getRolelist",
    }),
  },
  methods: {
    ...mapActions({
      roleActions: "role/roleActions",
    }),
    edit(id) {
      this.$emit("detail", id);
    },
    del(id) {
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "确定",
        cancelmButtonText: "取消",
        type: "warning",
      }).then(() => {
        delRole({ id, id }).then((res) => {
          if (res.data.code == 200) {
            successAlert(res.data.msg);
            this.roleActions();
          }
        });
      }).catch(()=>{
          this.$message({
              type:"info",
              message:"已取消删除"
          })
      });
    },
  },
  mounted() {
    this.roleActions();
  },
};
</script>

<style>
</style>


